"use client";

import { useState } from "react";
import { cn } from "@/fuctions/utils";

import {
  LayoutDashboard,
  Users,
  Building,
  PlusCircle,
  Eye,
  Settings,
} from "lucide-react";

const navigationItems = [
  {
    name: "Dashboard",
    icon: LayoutDashboard,
    href: "/dashboard",
    active: true,
  },
  {
    name: "User management",
    icon: Users,
    href: "/users",
    active: false,
  },
  {
    name: "Office management",
    icon: Building,
    href: "/offices",
    active: false,
  },
  {
    name: "Add appointment",
    icon: PlusCircle,
    href: "/appointments/add",
    active: false,
  },
  {
    name: "View appointment",
    icon: Eye,
    href: "/appointments",
    active: false,
  },
  {
    name: "Profile",
    icon: Settings,
    href: "/profile",
    active: false,
  },
];

export default function Sidebar({ className }: SidebarProps) {
  const [activeItem, setActiveItem] = useState("Dashboard");

  return (
    <div
      className={cn(
        "w-64 h-screen bg-white border-r border-[#E1E1E1] flex flex-col",
        className
      )}
    >
      {/* Logo Section */}
      <div className="p-6 border-b border-[#E1E1E1]">
        <div className="flex items-center gap-2">
          <div className="w-6 h-6 bg-[#24C453] rounded flex items-center justify-center">
            <span className="material-icons text-white text-base">people</span>
          </div>
          <span className="text-lg font-medium text-[#2C2C2C]">
            <span className="text-[#2C2C2C]">Kulan</span>
            <span className="text-[#24C453]">Desk</span>
          </span>
        </div>
      </div>

      {/* Navigation Section */}
      <nav className="flex-1 p-4">
        <ul className="space-y-1">
          {navigationItems.map((item) => {
            const isActive = activeItem === item.name;

            return (
              <li key={item.name}>
                <button
                  onClick={() => setActiveItem(item.name)}
                  className={cn(
                    "w-full flex items-center gap-3 px-3 py-2.5 text-sm font-medium rounded-lg transition-colors text-left",
                    isActive
                      ? "bg-[#E5F3E9] text-[#2C2C2C]"
                      : "text-[#999999] hover:bg-gray-50 hover:text-[#2C2C2C]"
                  )}
                >
                  <span
                    className={cn(
                      "material-icons text-lg flex-shrink-0",
                      isActive ? "text-[#24C453]" : "text-[#999999]"
                    )}
                  >
                    {item.icon}
                  </span>
                  <span>{item.name}</span>
                </button>
              </li>
            );
          })}
        </ul>
      </nav>
    </div>
  );
}

